import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { useAppSelector, useAppDispatch } from '../../app/hooks';
import Banner from '../../components/Banner';
import './home.css';
import Footer from '../../components/footer';
import { resetQsOptions } from '../../features/qsOptionsSlice';

const HomePage: React.FC = () => {
  const [surveyId, setSurveyId] = useState('');
  const [sKey, setSKey] = useState('');
  const [uKey, setUKey] = useState('');
  const [uuid, setUuid] = useState('');
  const [showAdvanced, setShowAdvanced] = useState(false);
  
  const navigate = useNavigate();
  const dispatch = useAppDispatch();
  const auth = useAppSelector(state => state.auth);
  
  // Reset Redux state and clear localStorage on component mount
  useEffect(() => {
    // Clear event tracking data
    window.localStorage.removeItem("eventRecords");
    
    // Reset Redux state for survey options
    dispatch(resetQsOptions());
    
    // Log that we've cleaned up state
    console.log("HomePage: Reset state and cleared localStorage");
  }, [dispatch]);
  
  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    if (surveyId.trim()) {
      // Build the URL with query parameters
      let url = `/survey/${surveyId.trim()}`;
      
      // Add query parameters if they exist
      const params = new URLSearchParams();
      
      if (sKey.trim()) params.append('sKey', sKey.trim());
      if (uKey.trim()) params.append('uKey', uKey.trim());
      if (uuid.trim()) params.append('uuid', uuid.trim());
      
      // Add parameters to URL if any exist
      const queryString = params.toString();
      if (queryString) {
        url += `?${queryString}`;
      }
      
      navigate(url);
    }
  };
  
  const handleLogin = () => {
    navigate('/login');
  };
  
  const toggleAdvanced = () => {
    setShowAdvanced(!showAdvanced);
  };
  
  return (
    <>
      <Banner title="Quadratic Survey">
        <div className="auth-section">
          <span className="user-status">{auth.isAuthenticated ? auth.user?.email || 'User' : 'Guest'}</span>
          {!auth.isAuthenticated && (
            <button className="login-button" onClick={handleLogin}>
              Login
            </button>
          )}
          {auth.isAuthenticated && (
            <button className="projects-button" onClick={() => navigate('/designer')}>
              My Projects
            </button>
          )}
        </div>
      </Banner>
      
      <div className="home-container">
      
      <div className="survey-entry">
        <h1>Begin Survey</h1>
        <p>Enter survey ID below</p>
        
        <form onSubmit={handleSubmit}>
          <input
            type="text"
            value={surveyId}
            onChange={(e) => setSurveyId(e.target.value)}
            placeholder="Survey ID"
            className="survey-input"
            required
          />
          
          {/* Advanced options toggle */}
          <div className="advanced-toggle">
            <button 
              type="button" 
              className="toggle-button" 
              onClick={toggleAdvanced}
            >
              {showAdvanced ? '- Hide Advanced Options' : '+ Show Advanced Options'}
            </button>
          </div>
          
          {/* Advanced options section */}
          {showAdvanced && (
            <div className="advanced-options">
              <div className="option-field">
                <label htmlFor="sKey">Static Key (sKey):</label>
                <input
                  id="sKey"
                  type="text"
                  value={sKey}
                  onChange={(e) => setSKey(e.target.value)}
                  placeholder="For password-protected surveys"
                  className="option-input"
                />
              </div>
              
              <div className="option-field">
                <label htmlFor="uKey">Unique Key (uKey):</label>
                <input
                  id="uKey"
                  type="text"
                  value={uKey}
                  onChange={(e) => setUKey(e.target.value)}
                  placeholder="For unique participant identification"
                  className="option-input"
                />
              </div>
              
              <div className="option-field">
                <label htmlFor="uuid">UUID:</label>
                <input
                  id="uuid"
                  type="text"
                  value={uuid}
                  onChange={(e) => setUuid(e.target.value)}
                  placeholder="For resuming a previous session"
                  className="option-input"
                />
              </div>
              
              <div className="option-info">
                <p><strong>Note:</strong> These fields are only required for specific surveys or to resume a previous session.</p>
              </div>
            </div>
          )}
          
          <button type="submit" className="submit-button">
            Start Survey
          </button>
        </form>
        
        {/* Demo Survey Button - now placed below the form */}
        <div style={{ marginTop: '20px', textAlign: 'center' }}>
          <button 
            onClick={() => navigate('/survey/680f38261354f9f2000e5db8')}
            className="submit-button"
          >
            Start Demo Survey
          </button>
          <p style={{ fontSize: '14px', marginTop: '8px', color: '#666' }}>
            CHI 2025 Conference Location Survey
          </p>
        </div>
      </div>
      </div>

      <Footer />
    </>
  );
};

export default HomePage;